/*========Modal=====*/
.resp-tab-content {
  display: block;
}
.reveal-modal-bg {
    position: fixed;
    height: 100%;
    width: 100%;
    background: #000;
    background: rgba(0,0,0,.5);
    z-index: 99999999;
    display: none;
    top: 0;
    left: 0;
    overflow-y: scroll;
    overflow-x: hidden;
}
.reveal-modal {
    top: 40px;
    left: calc(50% - 20px);
    margin-left: -40%;
    width: 80%;
    background: bottom repeat-x #fff;
    position: absolute;
    z-index: 101;
    padding: 0px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-box-shadow: 0 0 10px rgba(0,0,0,.4);
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,.4);
    box-shadow: 0 0 10px rgba(0,0,0,.4);
    animation-name: animatetop;
    animation-duration: 0.4s
}
.reveal-modal_small{
    left: calc(50% - 190px);
    margin-left: 0%;
    max-width: 381px;
    }

@media (max-width: 600px) {
.reveal-modal {
    top: 40px;
    left: calc(50% - 0px);
    margin-left: -50%;
    width: 100%;
}

.reveal-modal_small{
    left: calc(50% - 160px);
    margin-left: 0%;
     width: 320px;
    }
    
    
.reset_pass_btn{
  display: block;
  margin-top: 10px;  
}

